.content {
  margin-top: 5rem;
  display: flex;
  padding: 30px;
  height: 100%;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
}
.content h1 {
  font-size: x-large;
}
.content2 {
  justify-content: center;
}
.content3 {
  justify-content: center;
}
.content p {
  font-size: x-large;
}
.content img {
  display: block;
  align-self: center;
  text-align: center;
}
.content ul {
  display: block;
}
.content li {
  font-size: large;
  margin-bottom: 10px;
}
@media (max-width : 480px) {
     .content h1 {
      font-size: small;
     }
     .content li {
       font-size: x-small;
     }
     .content p {
       font-size: small;
     }
  
}