.navbar {
  width: 100%;
  position: fixed;
  top: 0;
  height: 8vh;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}
.list {
  margin-left: 10%;
  width: 40%;
  display: block;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.list_item {
  display: block;
  float: left;
  color: rgb(150, 150, 150);
  font-size: 0.5vw;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}
.anchor {
  text-decoration: none;
  color: inherit;
  outline: 0;
}
.anchor:active {
  color: black;
}
.list_item:hover {
  color: rgb(65, 65, 65);
}
.side_list {
  margin-right: 10%;
  width: 20%;
  display: block;
  list-style: none;
  display: flex;
  justify-content: flex-end;
}
.side_buttons {
  float: right;
  display: block;
  margin-right: 2vw;
  height: 50%;
}
.login {
  width: 12vw;
  height: 4vh;
  background-color: white;
  color: rgb(0, 162, 255);
  border-radius: 3px;
  border: 1px solid rgb(0, 162, 255);
  font-size: 1.2vw;
  font-style: bold;
  cursor: pointer;
}
.login:hover {
  box-shadow: 0 0 1px 0.5px rgba(0, 0, 0, 0.5);
}
.signup {
  width: 12vw;
  height: 4vh;
  background-color: rgb(0, 162, 255);
  color: white;
  border-radius: 3px;
  border: none;
  font-size: 1.2vw;
  font-style: bold;
  cursor: pointer;
}
.signup:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
@media (max-width : 480px) {
   .list {
     margin-left: -10%;
     width: auto;
     justify-content: flex-start;
   }
   .side_list {
     margin: 0;
   }
   .list_item {
    font-size: 1vw;
    display: block;
    margin-left: 1vw;
   }
   .list_item img {
     width: 150px;
   }
}
@media (max-width : 768px) {
  .list_item {
    font-size: 1vw;
    display: block;
    margin-left: 1vw;
   }
   .side_list {
    margin: 0;
  }
}
