.mainscreen {
  position: absolute;
  height: 100vh;
  width: 80%;
  margin-left: 20%;
  margin-top: -5rem;
  display: grid;
  grid-template-columns: 60% 40%;
  background-color: white;
}
.content_section {
  background-color: white;
  height: 100%;
}
.switch_section {
  background-color: white;
  height: 100%;
}
@media (max-width : 480px) {
  .content_section {
    text-align: center;
  }
}


