.switch {
  height: 100%;
  border-left: 1px solid grey;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}
.slider_bar {
  margin-top: 5rem;
  margin-left: 2vw;
  display: flex;
  justify-content: center;
  display: block;
  font-size: medium;
  justify-content: center;
  text-align: center;
}
@media (max-width : 480px) {
    .slider_bar {
      margin-left: 0;
      text-align: center;
    }
    .slider_bar h2 {
      font-size: small;
    }
}
